import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'gatsby'
import { useTheme, createUseStyles } from 'react-jss'
import { getButtonStyles } from 'frames-jss'
import cx from 'classnames'

const useStyles = createUseStyles((theme) => ({
  button: {
    ...getButtonStyles(theme).button,
    textDecoration: 'none',
  },
}))

const Button = ({ children, href, className, ...props }) => {
  const theme = useTheme()
  const styles = useStyles(theme)

  return href ? (
    <Link to={href} className={cx(styles.button, className)} {...props}>
      {children}
    </Link>
  ) : (
    <button className={cx(styles.button, className)}>{children}</button>
  )
}

Button.propTypes = {
  children: PropTypes.node.isRequired,
  href: PropTypes.string,
  className: PropTypes.string,
}

export default Button
